}

.slider .h1 {
  margin-top: 9.2%;
  margin-bottom: 4.3%;
}

.slider .carousel-inner h3 {
  background-color: rgba(20, 49, 68, 0.6);
  padding: 20px;
  animation: anime-H3 1.3s ease-out;
}

.slider .carousel-inner p {
  font-size: 18px;
  background-color: rgba(30, 29, 29, 0.6);
  padding: 20px;
  animation: anime-P 1.6s ease-out;
}

@keyframes anime-H3 {
  from {
    opacity: 0;
    transform: translateX(-2000px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes anime-P {
  from {
    opacity: 0;
    transform: translateX(2000px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

