@import "project.less";
@import "smart-grid.less";

.reset();

.body {
  font-family: Roboto, sans-serif;
}

.wrapper {
  .wrapper();
}

.debug {
  .debug(rgba(0, 0, 0, 0.5), 1px solid #ff0);
}

h1, .h1 {
  font-size: @font_36;
  text-align: center;
  margin: 30px 0;
}

h2, .h2 {
  font-size: @font_34;
  text-align: center;
  margin: 24px 0;
}

h3, .h3 {
  font-size: @font_28;
  text-align: center;
  margin: 18px 0;
}

h4, .h4 {
  font-size: @font_20;
  text-align: center;
  margin: 10px 0;
}

h5, .h5 {
  font-size: @font_18;
  text-align: center;
  margin: 8px 0;
}

h6, .h6 {
  font-size: @font_16;
  text-align: center;
  margin: 6px 0;
}
.up {

  background-color: @color_apple-green;
  border-radius: 10px;
  color: #000;
  cursor: pointer;
  display: none;
  padding: 1% 2%;
  position: fixed;
  top: 90%;
  left: 87%;
  transition: 1s;
  &:hover{
    background-color: #000000;
    color: #ffffff;
  }
}

.head {
  display: flex;
  justify-content: center;

  .header {
    max-width: 1440px;
    width: 100%;
    background: url("/img/building1080592.png") no-repeat;
    background-size: contain;
    position: relative;

    .spacer {
      padding-top: 66.666%;
      width: 100%;
    }

    .items {
      top: 0;
      left: 0;
      position: absolute;
      width: 100%;
      height: 100%;
      margin-left: 0;
      margin-right: 0;
      display: flex;
      flex-wrap: wrap;

      .first {
        width: 100%;
        display: flex;
        height: 45%;
        .md-block({ .item:nth-child(4) {
          display: none;
        } });
        .ls-block({ .item:nth-child(2) {
          display: none;
        } });

        .black {
          display: flex;
          flex-direction: column;
          justify-content: center;

          h2 {
            width: 100%;
            font-weight: 300;
            font-family: @font_light;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            text-align: center;
            transition: 1s;
            color: #ffffff;
            .ld-block({ font-size: @font_26; });
            .s-block({ font-size: @font_22; });
            .sm-block({ font-size: @font_20; margin-top: 4%; margin-bottom: 4%; });
            .md-block({ font-size: @font_18; });
            .xs-block({ font-size: @font_13; });
          }
          .green {
            color: @color_apple-green;
            font-weight: 900;
            font-family: @font_black;
          }
          background: @color_charcoal-grey;
        }
      }
      .second {
        width: 100%;
        display: flex;
        height: 55%;
        .md-block({ .item:nth-child(3) {
          display: none;
        } });
        .ls-block({ .item:nth-child(1) {
          display: none;
        } });

        .black {
          display: flex;
          flex-direction: column;
          justify-content: center;
          font-family: @font_roboto;
          align-items: center;
          h1 {
            font-weight: 300;
            font-style: normal;
            font-stretch: normal;
            line-height: normal;
            letter-spacing: normal;
            .ld-block({ font-size: 26px; line-height: inherit });
            .md-block({ font-size: @font_22; margin-top: 0; });
            .xs-block({ font-size: @font_18; margin-top: 0; display: none; });

          }
          h4 {
            width: 65%;
            color: @color_white;
            font-weight: 300;
            .ld-block({ width: 75%; font-size: @font_18; });
            .s-block({ font-size: @font_16; margin-top: 4%; margin-bottom: 4%; });
            .sm-block({ width: 85%; font-size: @font_16; margin-top: 2%; margin-bottom: 2%; });
            .md-block({ width: 88%; font-size: @font_14; margin-top: 1%; margin-bottom: 1%; });
            .md-block({ width: 90%; font-size: @font_12; margin-top: 1%; margin-bottom: 1%; });
            .xs-block({ font-size: @font_11; });

            a {

              color: @color_white;
              font-style: normal;
              font-stretch: normal;
              line-height: 1.67;
              letter-spacing: normal;
              text-align: center;
            }
          }
          .green {
            a {
              color: @color_apple-green;
            }
            color: @color_apple-green;
          }
          background: @color_charcoal-grey;
        }
      }

      .item {
        width: 25%;
        margin: 0 0 0 0;
        transition: 1s;
        animation: forwards;
        .md-block({ width: 33.333333%; });
        .ls-block({ width: 50%; });

      }

      .opacity {
        background: @color_white-four;
        opacity: 0.5;
      }
    }
  }
}

.main {

  .about_company {
    margin-top: 9.2%;
    font-size: 36px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    font-family: @font_roboto;
    letter-spacing: normal;
    text-align: left;
    color: @color_apple-green;
    span {
      font-weight: 900;
    }
  }
  .items {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    max-height: 620px;
    position: relative;
    .s-block({ flex-direction: row; max-height: none; justify-content: center; });

    .images {
      background: black;
      width: 39.3%;
      min-width: 450px;
      transition: 1s;
      animation: forwards;
      .s-block({ order: -2; min-width: unset; width: 45%; });
      .md-block({ width: 85%; img {
        width: 100%
      } });
    }
    .enclosed {
      position: absolute;
      top: 49%;
      right: 37%;
      flex-basis: 415px;
      border: solid 1px #979797;
      background: black;
      transition: 1s;
      .s-block({ position: unset; flex-basis: unset; width: 45%; margin-left: 3%; margin-top: -2%; });
      .md-block({ display: none; });
    }

    .h5 {
      width: 48.1%;
      font-size: 18px;
      padding-left: 18px;
      font-family: @font_roboto;
      font-weight: normal;
      font-style: normal;
      font-stretch: normal;
      line-height: normal;
      letter-spacing: normal;
      text-align: left;
      color: #000000;
      border-left: solid 2px @color_apple-green;
      margin-bottom: 12%;
      transition: 1s;
      .s-block({ order: -2; width: 45%; margin: 0 0 auto 3%; padding-left: 2%; });
      .md-block({ padding: 3% 0 2% 0; border-left: none; border-top: solid 2px @color_apple-green; width: 85%; margin: 2% 0 2% 0; });

    }
    .infos {
      width: 33%;
      margin: 0 0 0 auto;
      transition: 1s;
      .s-block({ order: -1; width: 45%; margin: 0; align-self: flex-end; });
      .md-block({ width: 85%; margin: 0; });
      .ls-block({ width: 100%; });
      .info {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100px;
        background: @color_white-three;
        border: 1px solid @color_apple-green;
        font-family: @font_roboto;
        color: @color_black;
        &:first-child {
          border-bottom: none;
        }
        span {
          opacity: 0.8;
          font-size: 40px;
          font-weight: bold;
          margin: 0 3%;
        }
        h6 {
          opacity: 0.8;
          font-size: 16px;
        }
      }
    }

  }
}

.services {
  background: @color_white-three;
  padding-top: 3%;
  margin-top: 5.6%;
  .title {
    text-align: left;
    margin: 0;
  }
  .items {

    .item {
      display: flex;
      margin-top: 5.2%;

      .sm-block({ flex-direction: column; });
      .xs-block({ position: relative });

      &:hover {
        .img {
          opacity: 1;
        }
        .text {
          background: @color_apple-green;
          .xs-block({ opacity: 1; transition: 1s; animation: forwards; });

          .h4 {
            color: @color_white;
            font-weight: 800;
          }
          .a {
            color: @color_white;
            font-weight: 800;
          }
        }
      }
      .img {
        display: flex;
        width: 73.7564322%;
        border: 1px solid @color_apple-green;
        transition: 1s;
        opacity: 0.5;
        .sm-block({ width: 100%; });
        .xs-block({ opacity: 1; });
      }
      .text {

        background: @color_white;
        width: 26.2%;
        border: 1px solid @color_apple-green;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        transition: 1s;
        animation: ease-in-out;
        .sm-block({ width: 100%; });
        .xs-block({ position: absolute; height: 100%; opacity: 0 });

        .h4 {
          text-align: left;
          width: 80%;
          transition: 1s;
          .sm-block({ margin-bottom: 1%; width: 90% });

        }
        .h6 {
          text-align: left;
          width: 80%;
          margin-top: 10%;
          margin-bottom: 15%;
          .sm-block({ margin-top: 2%; margin-bottom: 3%; width: 90% });
        }
        .a {
          width: 80%;
          transition: 1s;
          color: @color_black;
          .sm-block({ margin-bottom: 3%; width: 90% });

        }
      }
    }
  }
}

.slider {
  margin-bottom: 4.1%;
  .h1 {
    margin-top: 9.2%;
    margin-bottom: 4.3%;
  }
  .carousel-inner h3 {
    background-color: rgba(20, 49, 68, 0.6);
    padding: 20px;
    animation: anime-H3 1.3s ease-out;
  }
  .carousel-inner p {
    font-size: 18px;
    background-color: rgba(30, 29, 29, 0.6);
    padding: 20px;
    animation: anime-P 1.6s ease-out;
  }

  @keyframes anime-H3 {
    from {
      opacity: 0;
      transform: translateX(-2000px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
  @keyframes anime-P {
    from {
      opacity: 0;
      transform: translateX(2000px);
    }
    to {
      opacity: 1;
      transform: translateX(0);
    }
  }
}

.useful {
  background: @color_apple-green;
  padding-bottom: 3%;
  margin-bottom: 6%;
  .h1 {
    text-align: left;
    color: @color_white;
    font-weight: 300;
    padding-top: 5%;
    padding-bottom: 3%;
    padding-left: 2%;

  }
  .content {
    display: flex;
    border-left: 2px solid white;
    padding-left: 2%;
    .text {
      width: 63%;
      .h5 {
        width: 90%;
        text-align: left;
        font-family: Helvetica, sans-serif;
        &:first-child {
          .sm-block({ width: 90%; });
        }
        &:nth-child(2) {
          .sm-block({ width: 90%; });
        }
        .sm-block({ width: 145%; });
        .xs-block({ &:first-child {
          width: 140%;
        } &:nth-child(2) {
            width: 140%;
          } width: 140%; });

      }
    }

    .img {
      width: 30%;
      display: flex;
      justify-content: center;
      align-items: center;
      .sm-block({ align-items: flex-start; margin-top: 2% });
      .xs-block({ height: 200px; align-items: center; margin-top: 2%; display: none; });
      div {
        width: 80%;
      }
    }
  }
}

.footer {
  .h1 {
    .ld-block({ font-size: @font_26; });
    .m-block({ font-size: @font_24; });
  }
  .span {
    display: flex;
    justify-content: center;
    .green {
      color: @color_apple-green;
      font-weight: bold;
    }
    .h1 {
      text-align: left;
      font-size: @font_30;
      .ld-block({ font-size: @font_26; });
      .m-block({ font-size: @font_20; });
      .md-block({ font-size: @font_16; });
      .xs-block({ font-size: @font_14; });
    }
  }
  .content {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .item {
      display: flex;
      flex-direction: column;
      padding: 1rem;
      transition: 1s;
      .md-block({ width: 45%; justify-content: center; align-items: center });
    }
    .address {
      .s-block({ width: 20%; });
      .xs-block({ text-align: center })
    }
    .number {

    }
    .contacts {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      width: 25%;

      .h4 {
        width: 100%;
        white-space: nowrap;
        margin-top: 0;
        .ld-block({ font-size: @font_16; });
        .sm-block({ white-space: normal; });
      }
      .a {
        width: 22%;
        margin-left: 1%;
        margin-right: 1%;
      }
    }
    .mib {
      .h4 {
        margin-top: 0;
      }
      .h1 {
        font-family: BigCaslon;
        font-size: 50px;
        font-weight: 500;
        font-style: normal;
        font-stretch: normal;
        line-height: normal;
        letter-spacing: normal;
        color: #000000;
        transition: 1s;
        .md-block({ font-family: Sacramento; })
      }
    }
  }

}


.debug(@background, @outline) {
  height: 100vh;
  position: fixed;
  width: 100%;

  > div {
    height: inherit;
    .wrapper();

    > div {
      height: inherit;
      .row-flex();

      > div {
        .col();
        .size(1);
        height: inherit;
        outline: @outline;
        background: @background;
      }
    }
  }
}

